/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */
import { __ } from "@wordpress/i18n";

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */
import { useBlockProps, InspectorControls } from "@wordpress/block-editor";
import { useState } from '@wordpress/element';
import { PanelBody, TextControl, ToggleControl } from "@wordpress/components";
/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import "./editor.scss";

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @return {Element} Element to render.
 */
export default function Edit({ attributes, setAttributes }) {
	const { showStartingYear, startingYear } = attributes;
	const currentYear = new Date().getFullYear().toString();

	const [newCount, setNewCount] = useState(attributes?.count);

	let displayDate;
	if (showStartingYear && currentYear) {
		displayDate = `${startingYear} - ${currentYear}`;
	} else {
		displayDate = currentYear;
	}


    const handleCountIncrease = () => {
		setNewCount(newCount + 1);
		setAttributes({ count: newCount });
	}

	const handleCountDecrease = () => {
		setNewCount(newCount - 1);
		setAttributes({ count: newCount });
	}
	return (
		<>
			<InspectorControls>
				<PanelBody title={__("Settings", "copyright-date-block")}>
					<ToggleControl
						label={__("Show starting year", "copyright-date-block")}
						checked={showStartingYear}
						onChange={() =>
							setAttributes({ showStartingYear: !showStartingYear })
						}
					/>
					{showStartingYear && (
						<TextControl
							label={__("Starting year", "copyright-date-block")}
							value={startingYear || ""}
							onChange={(value) => setAttributes({ startingYear: value })}
						/>
					)}
				</PanelBody>
			</InspectorControls>
			<>
				<p {...useBlockProps()}>
					Â©{displayDate} {`count ${attributes?.count}`}
					<button onClick={handleCountIncrease} >Count+</button>
					<button onClick={handleCountDecrease} >Count-</button>
				</p>
	
			</>
		</>
	);
}
